import * as yup from 'yup';
import { isValidPhoneNumber } from 'libphonenumber-js';
import _isEmpty from 'lodash.isempty';
import config from '../../../config';

export const validationAddNewSchema = yup.object().shape({
	email: yup
		.string()
		.required('Email is require')
		.email('Please enter a valid email address.'),
	firstName: yup.string().required('First Name is require'),
	lastName: yup.string().required('Last Name is require'),
	phone: yup
		.string()
		.test(
			'Phone Number Test',
			'Please enter validate phone number',
			(value = '') => {
				if (_isEmpty(value)) return true;
				return isValidPhoneNumber(value, config.locate.countryCode);
			}
		),
});

export const validationEditSchema = yup.object().shape({
	dataTreePropertyId: yup
		.string()
		.required('Please search and select a property'),
});
