import {
	Button,
	Grid,
	Stack,
	TextField,
	Divider,
	CardContent,
	CardActions,
	FormHelperText,
	useTheme,
	Alert,
	Table,
	TableBody,
	TableContainer,
	MenuItem,
	Select,
	Typography,
} from '@material-ui/core';
import { FormikHelpers, useFormik } from 'formik';
import { useState, useEffect } from 'react';
import _get from 'lodash.get';
import { useMutation, gql, useQuery } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/core/styles';
import SubCard from '../../../ui-component/cards/SubCard';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { gridSpacing } from '../../../store/constant';
import InputLabel from '../../../ui-component/extended/Form/InputLabel';
import {
	TEditProperty,
	PropertyData,
	TProperty,
	TAddProperty,
} from '../../../types';
import SearchProperty from './components/SearchProperty';
import UploadFile, { TFile } from '../components/UploadFile';
import {
	updatePropertyData,
	selectDataTreeProperty,
} from '../../../graphql/mutations';
import {
	getProperty,
	listSupportedVestingTypes,
} from '../../../graphql/queries';
import { validationEditSchema } from './Validation';
import Loader from '../../../ui-component/Loader';
import TextRow from '../components/TextRow';
import { useSnackBar } from '../../../hooks';
import MainCard from '../../../ui-component/cards/MainCard';
import ApproveActionButton from './components/ApproveAction';
import { isApproved } from './Rules';

type Props = {
	id: string;
};

const useStyles = makeStyles(theme => ({
	container: {
		paddingTop: theme.spacing(1),
	},
}));

const PropertyEdit = ({ id }: Props) => {
	const classes = useStyles();
	const [images, setImages] = useState<TFile[]>([]);
	const showSnackBar = useSnackBar();
	const theme = useTheme();

	const [updatePropertyDataAction, { loading: isLoadingUpdateProperty }] =
		useMutation(gql(updatePropertyData));
	const { loading, error, data, refetch } = useQuery(gql(getProperty), {
		variables: { PropertyId: id },
	});
	const {
		loading: isLoadingVestingTypes,
		error: errorVestingTypes,
		data: vestingTypesData,
	} = useQuery(gql(listSupportedVestingTypes));
	const [selectDataTreePropertyAction] = useMutation(
		gql(selectDataTreeProperty)
	);

	const property = _get(data, 'getProperty', {});
	const propertyThumbnail = _get(property, 'Thumbnail', null);
	const dTPropertyId = _get(property, 'DTPropertyId');
	const propertyData = _get(property, 'PropertyData');
	const sellerInfo = _get(property, 'SellerInfo', {});
	const statusId = _get(property, 'StatusId');
	const approved = isApproved(statusId);
	const isDisableYearBuilt =
		approved || !_isEmpty(_get(propertyData, 'YearBuilt', 0));
	const isDisableVestingType =
		approved || !_isEmpty(_get(propertyData, 'VestingTypeMapped', ''));

	const taxReportTaxResourceStatus = _get(
		property,
		['ReportStatus', 'TaxReportTaxResourceStatus'],
		''
	);

	const listSupportedVestingTypesOptions = _get(
		vestingTypesData,
		'listSupportedVestingTypes',
		[]
	);

	const onSubmitHandler = async (
		values: TEditProperty,
		{ setStatus, setSubmitting }: FormikHelpers<TEditProperty>
	) => {
		try {
			const variables = {
				PropertyId: id,
				...(!isDisableVestingType && {
					VestingType: { VestingType: values.vestingType },
				}),
				...(!isDisableYearBuilt && { YearBuilt: { YearBuilt: values.yearBuilt } }),
				ListPriceRange: { Max: values.priceMax, Min: values.priceMin },
			};
			await updatePropertyDataAction({
				variables,
			});
			showSnackBar({
				message: 'Property has updated',
				alertSeverity: 'success',
			});
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({
				message,
			});
		}
	};

	const formik = useFormik({
		initialValues: {
			dataTreePropertyId: '',
			vestingType: 'Unknown',
			priceMin: 0,
			priceMax: 0,
			yearBuilt: '',
		},
		enableReinitialize: true,
		validationSchema: validationEditSchema,
		onSubmit: onSubmitHandler,
	});

	useEffect(() => {
		if (_isEmpty(data)) return;
		const formData = _get(data, 'getProperty', {});
		const listPriceRange = _get(formData, 'ListPriceRange', {});
		const propertyData = _get(formData, 'PropertyData', {});
		const { DTPropertyId: dataTreePropertyId } = formData;
		const max = _get(listPriceRange, 'Max', 0);
		const min = _get(listPriceRange, 'Min', 0);
		const yearBuilt =
			_get(propertyData, 'YearBuilt', '') ||
			_get(propertyData, 'YearBuiltOverride', '');
		const vestingType =
			_get(propertyData, 'VestingTypeMapped', '') ||
			_get(propertyData, 'VestingTypeOverride', '');
		formik.setFieldValue('dataTreePropertyId', dataTreePropertyId || '');
		formik.setFieldValue('priceMin', min);
		formik.setFieldValue('priceMax', max);
		formik.setFieldValue('yearBuilt', yearBuilt);
		formik.setFieldValue('vestingType', vestingType || 'Unknown');
	}, [data]);

	useEffect(() => {
		if (!propertyThumbnail) return;

		const { presignedUrl = '', name = '' } = propertyThumbnail;
		const thumbnail = Object.assign(new File([''], name), {
			preview: presignedUrl,
		});
		setImages([thumbnail]);
	}, [propertyThumbnail]);

	useEffect(
		() => () => {
			// Make sure to revoke the data uris to avoid memory leaks
			images.forEach(file => file.preview && URL.revokeObjectURL(file.preview));
		},
		[images]
	);

	if (loading || isLoadingVestingTypes || isLoadingUpdateProperty)
		return <Loader />;

	if (errorVestingTypes) {
		showSnackBar({
			message: 'Something went wrong',
		});
		return <></>;
	}

	if (error && _isEmpty(property)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Property not found
			</Alert>
		);
	}

	const onUploadFile = (acceptedFiles: File[]) => {
		setImages(
			acceptedFiles.map(file =>
				Object.assign(file, {
					preview: URL.createObjectURL(file),
				})
			)
		);
	};

	const onSelectPropertyHandler = async (property: PropertyData) => {
		try {
			const { PropertyId } = property;
			formik.setFieldValue('dataTreePropertyId', PropertyId || '');
			await selectDataTreePropertyAction({
				variables: {
					PropertyId: id,
					DataTreePropertyId: PropertyId,
				},
			});
			refetch();
		} catch (e) {
			const message = _get(e, 'message', 'Something went wrong');
			showSnackBar({ message });
		}
	};

	const {
		touched,
		errors,
		handleSubmit,
		values,
		handleChange,
		handleBlur,
		isSubmitting,
	} = formik;

	const isShowDataPropertyData = !_isEmpty(dTPropertyId && propertyData);

	return (
		<MainCard
			title={`#${id}`}
			content={false}
			secondary={<ApproveActionButton property={property} refetch={refetch} />}
		>
			<Grid container spacing={gridSpacing}>
				<Grid item xs={12}>
					<form noValidate onSubmit={handleSubmit}>
						<CardContent>
							<Grid container spacing={2} alignItems='center'>
								<Grid item lg={6} xs={12}>
									<Grid item>
										<SubCard title='Property Information'>
											<Grid item>
												<SearchProperty
													onSelect={onSelectPropertyHandler}
													disabled={approved}
												/>
											</Grid>
											{isShowDataPropertyData && (
												<Grid container className={classes.container} spacing={2}>
													<Grid item lg={6} xs={12}>
														<InputLabel>List Price Min</InputLabel>
														<TextField
															value={values.priceMin}
															fullWidth
															placeholder='List Price Min'
															onChange={handleChange}
															onBlur={handleBlur}
															error={touched.priceMin && Boolean(errors.priceMin)}
															helperText={touched.priceMin && errors.priceMin}
															name='priceMin'
														/>
													</Grid>
													<Grid item lg={6} xs={12}>
														<InputLabel>List Price Max</InputLabel>
														<TextField
															value={values.priceMax}
															fullWidth
															placeholder='List Price Max'
															onChange={handleChange}
															onBlur={handleBlur}
															error={touched.priceMax && Boolean(errors.priceMax)}
															helperText={touched.priceMax && errors.priceMax}
															name='priceMax'
														/>
													</Grid>
												</Grid>
											)}
											{isShowDataPropertyData && (
												<Grid item xs={12} lg={12}>
													<Grid container spacing={2} className={classes.container}>
														<Grid item lg={6} xs={12}>
															<InputLabel>Built Year</InputLabel>
															<TextField
																value={values.yearBuilt}
																fullWidth
																placeholder='Built Year'
																onChange={handleChange}
																onBlur={handleBlur}
																error={touched.priceMin && Boolean(errors.yearBuilt)}
																helperText={touched.priceMin && errors.yearBuilt}
																name='yearBuilt'
																disabled={isDisableYearBuilt}
															/>
														</Grid>
														<Grid item lg={6} xs={12}>
															<InputLabel>Vesting Type</InputLabel>
															<Select
																labelId='vesting-select'
																id='vesting-type'
																name='vestingType'
																value={values.vestingType}
																onChange={handleChange}
																label='Vesting Type'
																disabled={isDisableVestingType}
															>
																<MenuItem value='Unknown'>
																	<em>Unknown</em>
																</MenuItem>
																{!_isEmpty(listSupportedVestingTypesOptions) &&
																	listSupportedVestingTypesOptions.map((type: string) => (
																		<MenuItem key={type} value={type}>
																			{type}
																		</MenuItem>
																	))}
															</Select>
														</Grid>
													</Grid>
												</Grid>
											)}
											{isShowDataPropertyData && (
												<Grid item className={classes.container}>
													<TableContainer>
														<Table
															sx={{
																'& td': {
																	borderBottom: 'none',
																},
															}}
															size='small'
														>
															<TableBody>
																<TextRow<TProperty>
																	data={property}
																	source='PropertyNo'
																	label='Property No'
																/>
																<TextRow<TProperty>
																	data={property}
																	source='PropertyData.StreetAddress'
																	label='Street Address'
																/>
																<TextRow<TProperty>
																	data={property}
																	source='PropertyData.County'
																	label='County'
																/>
																<TextRow<TProperty>
																	data={property}
																	source='PropertyData.State'
																	label='State'
																/>
																<TextRow<TProperty>
																	data={property}
																	source='PropertyData.Zip'
																	label='Zip'
																/>
																<TextRow<TProperty>
																	data={property}
																	source='PropertyData.City'
																	label='City'
																/>
															</TableBody>
														</Table>
													</TableContainer>
												</Grid>
											)}
											{!isShowDataPropertyData && (
												<Typography
													className={classes.container}
													color='inherit'
													variant='subtitle1'
													component='div'
												>
													{taxReportTaxResourceStatus}
												</Typography>
											)}
											<input
												name='dataTreePropertyId'
												type='hidden'
												value={values.dataTreePropertyId}
												onChange={handleChange}
												onBlur={handleBlur}
											/>
											{touched.dataTreePropertyId && errors.dataTreePropertyId && (
												<FormHelperText error>{errors.dataTreePropertyId}</FormHelperText>
											)}
										</SubCard>
									</Grid>
									<br />
									{isShowDataPropertyData && (
										<Grid item>
											<SubCard title='Property Photo'>
												<UploadFile files={images} onDrop={onUploadFile} accept='.pdf' />
											</SubCard>
										</Grid>
									)}
								</Grid>

								<Grid item lg={6} xs={12}>
									<SubCard title='Seller Information'>
										<Grid item>
											<TableContainer>
												<Table
													sx={{
														'& td': {
															borderBottom: 'none',
														},
													}}
													size='small'
												>
													<TableBody>
														<TextRow<TAddProperty>
															data={sellerInfo}
															source='Email'
															label='Email'
														/>
														<TextRow<TAddProperty>
															data={sellerInfo}
															source='FirstName'
															label='First Name'
														/>
														<TextRow<TAddProperty>
															data={sellerInfo}
															source='LastName'
															label='Last Name'
														/>
														<TextRow<TAddProperty>
															data={sellerInfo}
															source='Phone'
															label='Phone'
														/>
													</TableBody>
												</Table>
											</TableContainer>
										</Grid>
									</SubCard>
								</Grid>
							</Grid>
						</CardContent>
						<Divider />
						<CardActions>
							<Grid item xs={12}>
								<Stack direction='row' justifyContent='flex-end'>
									<AnimateButton>
										<Button
											color='primary'
											variant='contained'
											type='submit'
											disabled={isSubmitting || approved}
										>
											Save
										</Button>
									</AnimateButton>
								</Stack>
							</Grid>
						</CardActions>
					</form>
				</Grid>
			</Grid>
		</MainCard>
	);
};

export default PropertyEdit;
